import * as React from 'react';
import './portal.css';
import AppLayout from './layout';
import ContentManagementSystem from './components/content-management-system';
import { ModalConfirmationProvider } from './context/confirmationContext';
import { ModalSuccessProvider } from './context/successModalContext';
import { PathViewProvider } from './context/pathViewContext';
import { EditCollapseProvider } from './context/editCollapseContext';

function App() {
  return (
    <ModalConfirmationProvider>
      <ModalSuccessProvider>
        <PathViewProvider>
          <EditCollapseProvider>
            <AppLayout>
              <ContentManagementSystem />
            </AppLayout>
          </EditCollapseProvider>
        </PathViewProvider>
      </ModalSuccessProvider>
    </ModalConfirmationProvider>
  );
}

export default App;
